.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;
  
    .image-container {
      width: 23%;
      padding-right: 15px;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name,
    .quantity,
    .price {
      width: 23%;
    }
  
    .quantity {
      padding-left: 20px;
      display: flex;


      .arrow{
        cursor: pointer;
        

      }

      .value{
        margin:0 10px;
      }
    }
  
    .remove-button {
      padding-left: 12px;
      cursor: pointer;
    }
  }
  
  @media screen and (max-width:800px) {
    .checkout-item {
      font-size: 15px;
      .quantity{
        .value {
          margin: 0 7px;
        }
      }
      .price {
        padding-left: 20px;
      }
    }
  }