.collection-item {
    width: 22vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
    margin-right: 15px;
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }
  
    .custom-button {
      width: 80%;
      opacity: 0.7;
      position: absolute;
      top: 255px;
      display: none;
    }
  
    &:hover {
      .image {
        opacity: 0.8;
      }
  
      .custom-button {
        opacity: 0.85;
        display: flex;
      }
    }
  
    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
      
  
      .name {
        width: 86%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 14%;
      }
    }
  }

  @media screen and (max-width:800px) {
    .collection-item {
      width: 85vw;
     

      
      .image {
        margin-top: 50px;
        margin-bottom: 10px;
        height: 90%;
      }

      
      
    }
    
  }
  