@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  
  font-family: "Roboto";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto';
  padding: 20px 60px;
}
 a{
   text-decoration: none;
   color: black;
}

@media screen and (max-width:800px) {
  body {
    padding: 20px 30px;
  }
}



.homepage{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding:20px 80px}.directory-menu{width:100%;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}.menu-item{min-width:30%;height:240px;-webkit-flex:1 1 auto;flex:1 1 auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .content{height:90px;padding:0 25px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid black}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}@media screen and (max-width: 800px){.homepage{padding:0}}

.menu-item{min-width:30%;height:240px;-webkit-flex:1 1 auto;flex:1 1 auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);transition:-webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{height:90px;padding:0 25px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 800px){.directory-menu{-webkit-flex-direction:column;flex-direction:column}}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}@media screen and (max-width: 800px){.custom-button{margin:10px 0;width:100%}}

.collection-item{width:22vw;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:350px;-webkit-align-items:center;align-items:center;position:relative;margin-right:15px}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:-webkit-flex;display:flex}.collection-item .collection-footer{width:100%;height:5%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:86%;margin-bottom:15px}.collection-item .collection-footer .price{width:14%}@media screen and (max-width: 800px){.collection-item{width:85vw}.collection-item .image{margin-top:50px;margin-bottom:10px;height:90%}}

.collection-preview{display:-webkit-flex;display:flex;width:100%;-webkit-flex-direction:column;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 800px){.collection-preview{-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.preview{-webkit-flex-direction:column;flex-direction:column}}

.collections-overview{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}

.collection-page{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items.collection-item{margin-bottom:30px}@media screen and (max-width: 800px){.collection-page{width:100%;-webkit-align-items:center;align-items:center}.collection-page .items{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}}

.cart-icon{width:45px;height:45px;position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:-webkit-flex;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:center;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin:50px auto}.cart-dropdown .cart-items{height:240px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.header{height:70px;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin-bottom:20px}.header .logo-container{height:100%;width:70px;padding:15px}.header .logo{height:100%;width:70px}.header .options{width:50%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}@media screen and (max-width: 800px){.header{-webkit-justify-content:space-between;justify-content:space-between}.header .options{width:100%}.header .logo-container{margin-right:20px;padding:0}.header .logo-container .logo{width:30px}}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:380px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.sign-in .title{margin:10px 0}.sign-in .buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 800px){.sign-in{width:95vw}.sign-in form{width:88%}.sign-in .buttons{-webkit-flex-direction:column;flex-direction:column;margin-bottom:20px}}

.sign-up{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}@media screen and (max-width: 800px){.sign-up{width:95vw;margin-top:40px}.sign-up form{width:88%}}

