.sign-in {
    width: 380px;
    display: flex;
    flex-direction: column;

    .title {
        margin: 10px 0;
    }

    .buttons {
        display: flex;
        justify-content: space-between;
    }
}

@media screen and (max-width:800px) {
    .sign-in{
        width: 95vw;
        
        form {
            width: 88%;
            
        }
        .buttons {
            flex-direction: column;
            margin-bottom: 20px;
            
            
        }
    }
}